function SearchBar({ placeholder }) {
    const navigate = useNavigate();
    const [inputValue, setInputValue] = useState("");
    const [filteredMovies, setFilteredMovies] = useState([]);
    const [notFound, setNotFound] = useState(false);

    const handleChange = (event) => {
        setNotFound(false);
        const wordEntered = event.target.value.trim();
        setInputValue(wordEntered);
        
        // Filter movie names based on the predefined array
        const newFilter = movieNames.filter((movieName) => {
            return movieName.toLowerCase().includes(wordEntered.toLowerCase());
        });

        setFilteredMovies([]);
        if (newFilter.length > 0) {
            setFilteredMovies(newFilter);
        }
        if (wordEntered.length === 0) {
            setFilteredMovies([]);
        }
    };

    const buttonSubmit = () => {
        let flag = false;

        for (let movieName of movieNames) {
            if (inputValue.toLowerCase() === movieName.toLowerCase()) {
                flag = true;
                break;
            }
        }
        if (flag) {
            navigate(`/search/${inputValue}`);
        } else {
            setNotFound(true);
        }
    };

    return (
        <div className="SearchBody">
            <div className="CompleteBar">
                <div className="BAR">
                    <input
                        placeholder={placeholder}
                        className="searchingbar"
                        type="text"
                        title="Search"
                        onChange={handleChange}
                        onKeyPress={(e) => {
                            if (e.key === "Enter") {
                                buttonSubmit();
                            }
                        }}
                    />
                    <button className="search-button" onClick={buttonSubmit}>
                        <i className="fas fa-search"></i>
                    </button>
                </div>
            </div>
            {notFound ? (
                <div className="NotFound">
                    Sorry! dude..I am unaware of this movie..!!
                </div>
            ) : null}
            {filteredMovies.length > 0 ? (
                <div className="searchList">
                    {filteredMovies.slice(0, 10).map((movieName) => (
                        <div
                            className="searchItem"
                            onClick={() => navigate(`/search/${movieName}`)}
                            key={movieName}
                        >
                            {movieName}
                        </div>
                    ))}
                </div>
            ) : null}
        </div>
    );
}

export default SearchBar;
